@import url(https://fonts.googleapis.com/css2?family=Encode+Sans+Semi+Expanded:wght@900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  box-sizing: border-box;
}

body::-webkit-scrollbar-button {
  display: none;
}

body {
  scroll-behavior: smooth;
}

.root {
  scroll-behavior: smooth;
}

body::-webkit-scrollbar {
  width: 5px;
  border-left: 1px solid #E6ECF8;

}

html {
  scroll-behavior: smooth;
}

body::-webkit-scrollbar-thumb {
  background-color: #141c3a;
}

body::-webkit-scrollbar-button {
  display: none;
}

#root #root::-webkit-scrollbar {
  width: 6px;
  border-left: 1px solid #E6ECF8;

}

#root::-webkit-scrollbar-thumb {
  background-color: #141c3a;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}
.navbar{display:flex;justify-content:center;background:#fff;position:fixed;top:0;width:100vw;border-bottom:1px solid -webkit-link;border-bottom-left-radius:20px;border-bottom-right-radius:20px}.navbar .navbar-items{display:inline-flex}.navbar .navbar-items .navbar-item{color:#7510F7;text-decoration:none;padding:10px}.navbar .navbar-items:nth-child(2){display:none}@media (min-width: 600px){.navbar{justify-content:space-between}.navbar .navbar-items{margin-top:10px}.navbar .navbar-items:nth-child(1){margin-left:20px}.navbar .navbar-items:nth-child(2){margin-right:20px;display:inline-flex}}@media (max-width: 350px){.navbar{display:none}}

.info{width:100%;margin-top:10vw;display:flex;flex-direction:column;justify-content:center}.info .info-body{display:flex;flex-direction:column;justify-content:space-around;align-items:center}.info .info-body h1{color:#141c3a;margin-top:10vh;margin-bottom:1.5rem;font-weight:900;line-height:1.125;font-size:3.0rem !important;font-family:'Encode Sans Semi Expanded', sans-serif}.info .info-body h2{color:rgba(10,10,10,0.9);margin-bottom:1.5rem;font-weight:300;line-height:1.5;font-size:1.5rem !important}.info .info-body img{width:220px;margin:2rem 0}.info .info-footer img{width:600px}@media (max-width: 600px){.info{margin-top:10vh}.info .info-body h1{margin-top:10%;font-size:1.3rem !important;line-break:word-break;line-height:1.3}.info .info-body h2{font-size:0.8rem !important}.info .info-body img{width:150px}.info .info-footer img{max-width:300px}}@media (max-width: 350px){.info .info-body h1{font-size:1.5rem !important;margin:0}.info .info-body h2{font-size:0.8rem !important}.info .info-footer img{width:100vw}}

.about{color:#fff;width:100vw;background:#7510F7;display:flex;justify-content:center;margin-top:-4.4px}.about .container{width:70%;margin-bottom:8rem}.about .container h1{margin-top:1rem;font-size:1.8rem !important;margin-bottom:1.5rem;line-break:word-break}.about .container h2{font-size:1.25rem !important;font-weight:200;letter-spacing:2px;line-height:1.5;line-break:word-break}@media (max-width: 600px){.about{padding-bottom:5rem}.about .container{width:90%}.about .container h1{font-size:1.5rem !important;margin-bottom:2rem;line-break:word-break}.about .container h2{font-size:1.1rem !important;line-height:1.5;line-break:word-break}}

.main-content{width:100%;scroll-behavior:smooth;display:flex;flex-direction:column;align-items:center}.main-content::-webkit-scrollbar{width:6px;border-left:1px solid #E6ECF8}.main-content::-webkit-scrollbar-thumb{background-color:#141c3a}.main-content::-webkit-scrollbar-button{display:none}

.skills{width:100%;margin-top:-80px;display:flex;margin-bottom:60px;justify-content:center}.skills .box-container{width:70%}.skills .box-container .box{box-shadow:0 5px 5px 0 rgba(233,240,243,0.5),0 0 0 1px #E6ECF8;display:flex;background:#fff;border-top-left-radius:20px;border-top-right-radius:20px;overflow:hidden}.skills .box-container .box .column{text-align:center !important;padding:70px 45px;border:1px solid #E6ECF8;width:50%}.skills .box-container .box .column img{width:50px}.skills .box-container .box .column .title{color:#141c3a;margin:1.2rem 2rem}.skills .box-container .box .column .subtitle{font-weight:200;line-height:30px;font-size:18px;margin:2rem 2rem}.skills .box-container .box .column .item{margin:3rem 3rem;margin-bottom:2.5rem}.skills .box-container .box .column .item h2{color:#7510F7 !important;font-size:18px !important;font-weight:500;letter-spacing:0.5px;line-height:30px;margin-bottom:0.7rem}.skills .box-container .box .column .item p{font-weight:200;line-height:30px;font-size:18px}.skills .box-container .box .column .item p ul{padding:0;list-style:none;display:flex;flex-direction:column;justify-content:center;align-items:center}@media (max-width: 1300px){.skills .box-container{width:80%}.skills .box-container .box{flex-direction:column}.skills .box-container .box .column{width:100%}}@media (max-width: 350px){.column img{width:35px}.column .title{font-size:1.3rem !important}.column .subtitle{font-size:0.7rem !important}.column .item h2{font-size:0.7rem !important}.column .item p{font-size:0.7rem !important}}

.work{width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center;margin-bottom:10rem}.work h1{color:#141c3a;font-size:2rem;margin-bottom:5rem}.work .project-menu{width:50%;margin:0;display:grid;grid-template-columns:1fr 1fr;grid-template-rows:repeat(3, 1fr);justify-content:space-evenly;align-items:center}.work .project-menu .project{position:relative;width:80%;height:70%;justify-self:center;display:flex;justify-content:center;align-self:center;border-radius:20px;overflow:hidden;transition:0.9s}.work .project-menu .project img{transition:0.9s;width:100%;height:100%}.work .project-menu .project::before{content:'';position:absolute;width:100%;height:100%;z-index:1;top:0;left:0;background:#141c3a9f}.work .project-menu .project:hover img{transform:scale(1.3, 1.3)}.work .project-menu .project:hover .caption{opacity:1}.work .project-menu .project .caption{display:grid;grid-template-columns:1fr;padding:10%;justify-content:center;width:100%;height:100%;position:absolute;z-index:1000;top:0;left:0;background:#141c3a;color:white;opacity:0;transition:0.9s;-webkit-animation-delay:0.2s;animation-delay:0.2s}.work .project-menu .project .caption p{margin-bottom:10%;font-size:1.3rem !important}.work .project-menu .project .caption a{justify-self:center;align-self:flex-end;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;background:transparent;color:#fff;text-decoration:none;border:2px solid #7510F7;border-radius:20px;transition:0.5s;padding:10px}.work .project-menu .project .caption a.wop:hover{background:#555}.work .project-menu .project .caption a.cv::after{content:' >'}.work .project-menu .project .caption a.cv:hover{background:#7510F7}@media (max-width: 600px){.work .project-menu{width:100%;grid-template-columns:1fr}.work .project-menu .project{margin-left:0}.work .project-menu .project img{width:90vw;height:90vw}}

.contact{color:#fff;width:100vw;padding:5rem;background:#7510F7;display:flex;justify-content:center;align-items:center;flex-direction:column}.contact h1{margin-bottom:5rem}.contact .social-links{display:flex;flex-direction:row;justify-content:center}.contact .social-links a{font-size:1.3rem !important;margin-left:2rem;text-decoration:none;color:#fff;width:50px;height:50px;padding:10px;border:2px solid rgba(255,255,255,0.3);border-radius:50%;transition:0.7s}.contact .social-links a:hover{color:#7510F7;background:#fff}

